.card{  
  width: 272px;  
  border-radius: 12px;
  box-shadow: 2px 4px 9px 0px #A68DAE47; 
  transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1);
}
  
.card:hover .conteinerLincCart{
  display: flex;
}
  
.conteiner{
  padding: 12px; 
}

.options{
  display: flex;
  gap: 14px;
  margin-bottom: 202px;
}


.categoria{
  padding: 6px 12px;
  background-color: var(--background-color);
  border-radius: 8px;
  color:var(--color-additional7);
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
}
.priority{
  padding: 6px 12px;
  background-color:var(--background-color);
  border-radius: 8px;
 
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
} 

  
.dateConteiner{
  display: flex;
  justify-content: space-between;
  padding: 0 4px;
  color: var(--color-additional7);
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
    
}
  
.titleCard{
  margin: 16px 0;
  padding: 0 4px;
  
  color: var(--color-text-acsent);
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}
  
.description{ 
  overflow: hidden;
  padding: 4px;
  
  text-align: justify;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  height: 72px;
}
  
.conteinerLincCart{
  display: none;
  justify-content: right;
  margin-top: 8px;  
}

.linkCard{  
  padding: 10px 24px; 
  color: var(--background-color);  
  
  background-color:var(--color-additional7);
  border-radius: 8px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  cursor: pointer;
  transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1), background 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

.linkCard:hover ,.linkCard:focus {
  background: var(--color-additional7);
  box-shadow: 2px 4px 9px 0px rgba(166, 141, 174, 0.28);
}

@media (min-width: 768px) and (max-width: 1279px) {
  .card {        
    width: 332px;        
  } 
}
  
@media (min-width: 1280px) {
  .card {
    width: 302px;    
  }  
}